import { useTranslation } from 'react-i18next';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { useAuth } from '../context/AuthContext';
import API_BASE_URL from '../config';
import '../styles/Prices.css';

const Prices = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const { user } = useAuth();
  const [isOpen, setIsOpen] = useState(false);
  const [cart, setCart] = useState([]);

  const services = [
    { name: 'PriceStandardRoom', cost: '2000 /', desc: 'PriceStandardRoomDesc' },
    { name: 'PriceLuxRoom', cost: '3500 /', desc: 'PriceLuxRoomDesc' },
    { name: 'PriceSauna', cost: '1500 /', desc: 'PriceSaunaDesc' },
    { name: 'PriceBanya', cost: '2000 /', desc: 'PriceBanyaDesc' },
    { name: 'PriceKitchen', cost: '1000 /', desc: 'PriceKitchenDesc' },
    { name: 'PriceBanquet', cost: '5000 / + 500 /', desc: 'PriceBanquetDesc' },
  ];

  const kitchenMenuOptions = [
    { name: 'GrilledChicken', cost: 800, category: 'main' },
    { name: 'PastaCarbonara', cost: 600, category: 'main' },
    { name: 'BeefSteak', cost: 1000, category: 'main' },
    { name: 'VegetableSoup', cost: 400, category: 'main' },
    { name: 'FishFillet', cost: 900, category: 'main' },
    { name: 'CheesePlatter', cost: 500, category: 'snack' },
    { name: 'Bruschetta', cost: 300, category: 'snack' },
    { name: 'SpringRolls', cost: 350, category: 'snack' },
    { name: 'Lemonade', cost: 200, category: 'drink' },
    { name: 'Cola', cost: 150, category: 'drink' },
    { name: 'Beer', cost: 250, category: 'beer' },
  ];

  const getPrice = (serviceName, defaultCost) => {
    const price = JSON.parse(localStorage.getItem('servicePrices'))?.[serviceName.replace('Price', '')];
    return price ? price.ru || defaultCost : defaultCost;
  };

  useEffect(() => {
    const timer = setTimeout(() => setIsOpen(true), 300);
    return () => clearTimeout(timer);
  }, []);

  const addToCart = (service) => {
    const serviceName = service.name.replace('Price', '');
    setCart((prev) => [
      ...prev,
      {
        ...service,
        name: serviceName,
        quantity: 1,
        ...(serviceName === 'StandardRoom' || serviceName === 'LuxRoom'
          ? { startDate: '', endDate: '' }
          : serviceName === 'Sauna' || serviceName === 'Banya'
          ? { date: '', time: '', duration: 1 }
          : serviceName === 'Kitchen'
          ? { date: '', time: '', duration: 1, menu: [] }
          : {}),
      },
    ]);
  };

  const removeFromCart = (index) => {
    setCart((prev) => prev.filter((_, i) => i !== index));
  };

  const updateCartItem = (index, field, value) => {
    setCart((prev) =>
      prev.map((item, i) =>
        i === index ? { ...item, [field]: value } : item
      )
    );
  };

  const updateKitchenMenu = (index, itemName) => {
    setCart((prev) =>
      prev.map((cartItem, i) =>
        i === index
          ? {
              ...cartItem,
              menu: cartItem.menu.includes(itemName)
                ? cartItem.menu.filter((m) => m !== itemName)
                : [...cartItem.menu, itemName],
            }
          : cartItem
      )
    );
  };

  const calculateTotalCost = () => {
    return cart.reduce((sum, item) => {
      const baseCost = parseFloat(getPrice(item.name, item.cost).split(' ')[0]);
      let itemCost = 0;

      if (item.name === 'StandardRoom' || item.name === 'LuxRoom') {
        if (item.startDate && item.endDate) {
          const days = Math.ceil((new Date(item.endDate) - new Date(item.startDate)) / (1000 * 60 * 60 * 24));
          itemCost = baseCost * days;
        }
      } else if (item.name === 'Sauna' || item.name === 'Banya' || item.name === 'Kitchen') {
        itemCost = baseCost * (item.duration || 1);
        if (item.name === 'Kitchen' && item.menu) {
          const menuCost = item.menu.reduce(
            (menuSum, menuItem) => menuSum + (kitchenMenuOptions.find((opt) => opt.name === menuItem)?.cost || 0),
            0
          );
          itemCost += menuCost;
        }
      } else {
        itemCost = baseCost * item.quantity;
      }

      return sum + itemCost;
    }, 0) + ' ';
  };

  const handleOrder = async () => {
    if (cart.length === 0) {
      alert(t('CartEmpty'));
      return;
    }

    const incompleteItems = cart.filter((item) => {
      if (item.name === 'StandardRoom' || item.name === 'LuxRoom') {
        return !item.startDate || !item.endDate;
      } else if (item.name === 'Sauna' || item.name === 'Banya' || item.name === 'Kitchen') {
        return !item.date || !item.time || !item.duration;
      }
      return false;
    });

    if (incompleteItems.length > 0) {
      alert(t('CompleteCartDetails'));
      return;
    }

    if (!user) {
      alert(t('PleaseLogin'));
      navigate('/login');
      return;
    }

    const order = {
      items: cart.map((item) => ({
        name: item.name,
        cost: getPrice(item.name, item.cost),
        ...(item.startDate && { startDate: item.startDate }),
        ...(item.endDate && { endDate: item.endDate }),
        ...(item.date && { date: `${item.date} ${item.time}:00` }),
        ...(item.duration && { duration: item.duration }),
        ...(item.menu && { menu: item.menu }),
      })),
      totalCost: calculateTotalCost(),
    };

    const token = localStorage.getItem('token');
    if (!token) {
      alert(t('PleaseLogin'));
      navigate('/login');
      return;
    }

    try {
      const response = await fetch(`${API_BASE_URL}/api/orders`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`,
        },
        body: JSON.stringify(order),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Failed to place order');
      }

      const newOrder = await response.json();
      const storedOrders = JSON.parse(localStorage.getItem('orders')) || [];
      storedOrders.push(newOrder);
      localStorage.setItem('orders', JSON.stringify(storedOrders));
      window.dispatchEvent(new Event('storage'));

      alert(t('OrderPlaced'));
      setCart([]);
      navigate('/profile');
    } catch (error) {
      console.error('Prices.jsx: Order placement error:', error);
      alert(t('OrderError') + ': ' + error.message);
    }
  };

  return (
    <section className="prices">
      <h2>{t('RestaurantMenu')}</h2>
      <motion.div
        className={`scroll ${isOpen ? 'open' : ''}`}
        initial={{ height: 0, opacity: 0 }}
        animate={{ height: isOpen ? 'auto' : 0, opacity: isOpen ? 1 : 0 }}
        transition={{ duration: 0.8, ease: 'easeOut' }}
      >
        <div className="scroll-content">
          <h3>{t('OurServices')}</h3>
          <ul className="price-list">
            {services.map((item, index) => (
              <motion.li
                key={index}
                className="price-item"
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: index * 0.1 }}
              >
                <div className="menu-details">
                  <span className="menu-name">{t(item.name)}</span>
                  <span className="menu-desc">{t(item.desc)}</span>
                </div>
                <div className="menu-action">
                  <span className="cost">{getPrice(item.name, item.cost)}</span>
                  <motion.button
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    onClick={() => addToCart(item)}
                  >
                    {t('AddToCart')}
                  </motion.button>
                </div>
              </motion.li>
            ))}
          </ul>

          {cart.length > 0 && (
            <div className="cart">
              <h3>{t('YourCart')}</h3>
              <ul className="cart-list">
                {cart.map((item, index) => (
                  <motion.li
                    key={index}
                    className="cart-item"
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ duration: 0.3 }}
                  >
                    <span>{t(item.name)} - {getPrice(item.name, item.cost)}</span>
                    {(item.name === 'StandardRoom' || item.name === 'LuxRoom') && (
                      <div className="cart-details">
                        <label>{t('StartDate')}:</label>
                        <input
                          type="date"
                          value={item.startDate}
                          onChange={(e) => updateCartItem(index, 'startDate', e.target.value)}
                          min={new Date().toISOString().split('T')[0]}
                        />
                        <label>{t('EndDate')}:</label>
                        <input
                          type="date"
                          value={item.endDate}
                          onChange={(e) => updateCartItem(index, 'endDate', e.target.value)}
                          min={item.startDate || new Date().toISOString().split('T')[0]}
                        />
                      </div>
                    )}
                    {(item.name === 'Sauna' || item.name === 'Banya') && (
                      <div className="cart-details">
                        <label>{t('Date')}:</label>
                        <input
                          type="date"
                          value={item.date}
                          onChange={(e) => updateCartItem(index, 'date', e.target.value)}
                          min={new Date().toISOString().split('T')[0]}
                        />
                        <label>{t('Time')}:</label>
                        <input
                          type="time"
                          value={item.time}
                          onChange={(e) => updateCartItem(index, 'time', e.target.value)}
                        />
                        <label>{t('DurationHours')}:</label>
                        <input
                          type="number"
                          min="1"
                          value={item.duration}
                          onChange={(e) => updateCartItem(index, 'duration', e.target.value)}
                          style={{ width: '60px' }}
                        />
                      </div>
                    )}
                    {item.name === 'Kitchen' && (
                      <div className="cart-details">
                        <label>{t('Date')}:</label>
                        <input
                          type="date"
                          value={item.date}
                          onChange={(e) => updateCartItem(index, 'date', e.target.value)}
                          min={new Date().toISOString().split('T')[0]}
                        />
                        <label>{t('Time')}:</label>
                        <input
                          type="time"
                          value={item.time}
                          onChange={(e) => updateCartItem(index, 'time', e.target.value)}
                        />
                        <label>{t('DurationHours')}:</label>
                        <input
                          type="number"
                          min="1"
                          value={item.duration}
                          onChange={(e) => updateCartItem(index, 'duration', e.target.value)}
                          style={{ width: '60px' }}
                        />
                        <label>{t('Menu')}:</label>
                        <div className="kitchen-menu">
                          {kitchenMenuOptions.map((option) => (
                            <div key={option.name}>
                              <input
                                type="checkbox"
                                id={`${index}-${option.name}`}
                                checked={item.menu.includes(option.name)}
                                onChange={() => updateKitchenMenu(index, option.name)}
                              />
                              <label htmlFor={`${index}-${option.name}`}>
                                {t(option.name)} - {option.cost} 
                              </label>
                            </div>
                          ))}
                        </div>
                      </div>
                    )}
                    <motion.button
                      whileHover={{ scale: 1.05 }}
                      whileTap={{ scale: 0.95 }}
                      onClick={() => removeFromCart(index)}
                      className="remove-btn"
                    >
                      {t('Remove')}
                    </motion.button>
                  </motion.li>
                ))}
              </ul>
              <p>{t('TotalCost')}: {calculateTotalCost()}</p>
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                onClick={handleOrder}
                className="order-btn"
              >
                {t('OrderNow')}
              </motion.button>
            </div>
          )}
        </div>
      </motion.div>
    </section>
  );
};

export default Prices;