import { useAuth } from '../context/AuthContext';
import { useTranslation } from 'react-i18next';
import { NavLink } from 'react-router-dom';

const Header = () => {
  const { t, i18n } = useTranslation();
  const { user, logout, isDarkMode, toggleDarkMode } = useAuth();
  const isAdmin = user && user.phone === '+79991234567';

  const changeLanguage = (lng) => {
    i18n.changeLanguage(lng);
  };

  return (
    <header className="header">
      <nav className="nav">
        <NavLink to="/">{t('Home')}</NavLink>
        <NavLink to="/about-complex">{t('AboutComplex')}</NavLink>
        <NavLink to="/services">{t('Services')}</NavLink>
        <NavLink to="/prices">{t('Prices')}</NavLink>
        {user ? (
          <>
            {isAdmin ? (
              <NavLink to="/admin">{t('Admin')}</NavLink>
            ) : (
              <NavLink to="/profile">{t('Profile')}</NavLink>
            )}
            <button onClick={logout}>{t('Logout')}</button>
          </>
        ) : (
          <>
            <NavLink to="/login">{t('Login')}</NavLink>
            <NavLink to="/register">{t('Register')}</NavLink>
          </>
        )}
        <button onClick={toggleDarkMode}>
          {isDarkMode ? t('LightMode') : t('DarkMode')}
        </button>
        <button onClick={() => changeLanguage('en')}>{t('English')}</button>
        <button onClick={() => changeLanguage('ru')}>{t('Russian')}</button>
      </nav>
    </header>
  );
};

export default Header;