import React, { useState, useEffect } from 'react';
import { useAuth } from '../context/AuthContext';
import { useLocation, useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import API_BASE_URL from '../config';
import '../styles/Booking.css';

const Booking = () => {
  const { user, isDarkMode } = useAuth();
  const { t } = useTranslation();
  const location = useLocation();
  const navigate = useNavigate();
  const [service, setService] = useState('');
  const [date, setDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [duration, setDuration] = useState(1);
  const [cost, setCost] = useState('');
  const [guestCount, setGuestCount] = useState('');
  const [banquetExtras, setBanquetExtras] = useState([]);
  const [kitchenMenu, setKitchenMenu] = useState([]);
  const [message, setMessage] = useState('');
  const [isInitialized, setIsInitialized] = useState(false);

  const formatDateForInput = (dateStr) => {
    if (!dateStr) return '';
    const date = new Date(dateStr);
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    const hours = String(date.getHours()).padStart(2, '0');
    const minutes = String(date.getMinutes()).padStart(2, '0');
    return `${year}-${month}-${day}T${hours}:${minutes}`;
  };

  useEffect(() => {
    if (location.state && !isInitialized) {
      const { service, date, endDate, duration, cost, guestCount, banquetExtras, kitchenMenu } = location.state;
      setService(service || '');
      setDate(date || '');
      setEndDate(endDate || '');
      setDuration(duration || 1);
      setCost(cost || '');
      setGuestCount(guestCount || '');
      setBanquetExtras(banquetExtras || []);
      setKitchenMenu(kitchenMenu || []);
      setIsInitialized(true);
      console.log('Booking.jsx: Initialized with state:', location.state);
    }
  }, [location.state, isInitialized]);

  const kitchenMenuOptions = [
    { name: 'CheesePlatter', cost: '500 ' },
    { name: 'Bruschetta', cost: '300 ' },
    { name: 'GrilledChicken', cost: '800 ' },
    { name: 'PastaCarbonara', cost: '600 ' },
    { name: 'Lemonade', cost: '200 ' },
    { name: 'Coffee', cost: '150 ' },
  ];

  const banquetExtraOptions = [
    { name: 'Decoration', cost: '2000 ' },
    { name: 'Music', cost: '3000 ' },
    { name: 'Photographer', cost: '4000 ' },
  ];

  const handleMenuSelection = (item) => {
    setKitchenMenu((prev) =>
      prev.includes(item.name) ? prev.filter((i) => i !== item.name) : [...prev, item.name]
    );
  };

  const handleBanquetExtras = (extra) => {
    setBanquetExtras((prev) =>
      prev.includes(extra) ? prev.filter((e) => e !== extra) : [...prev, extra]
    );
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!user) {
      setMessage(t('PleaseLogin'));
      navigate('/login');
      return;
    }

    if (!service || !date || !cost) {
      setMessage(t('CompleteBookingDetails'));
      return;
    }

    const isRoom = ['StandardRoom', 'LuxRoom'].includes(service);
    if (isRoom && !endDate) {
      setMessage(t('SelectEndDate'));
      return;
    }

    const token = localStorage.getItem('token');
    const bookingData = {
      service,
      cost,
      date,
      ...(endDate && { endDate }),
      ...(service !== 'StandardRoom' && service !== 'LuxRoom' && { duration }),
      isConfirmed: false,
      ...(guestCount && { guestCount: parseInt(guestCount, 10) }),
      ...(banquetExtras.length > 0 && { banquetExtras }),
      ...(kitchenMenu.length > 0 && { kitchenMenu }),
    };

    console.log('Booking.jsx: Submitting booking data:', bookingData);

    try {
      const response = await fetch(`${API_BASE_URL}/api/bookings`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`,
        },
        body: JSON.stringify(bookingData),
      });

      const result = await response.json();
      if (response.ok) {
        setMessage(t('BookingConfirmed'));
        setService('');
        setDate('');
        setEndDate('');
        setDuration(1);
        setCost('');
        setGuestCount('');
        setBanquetExtras([]);
        setKitchenMenu([]);
        navigate('/profile');
      } else {
        setMessage(`${t('BookingError')}: ${result.error}`);
      }
    } catch (error) {
      console.error('Booking.jsx: Submit error:', error);
      setMessage(`${t('BookingError')}: ${error.message}`);
    }
  };

  return (
    <div className={`booking-container ${isDarkMode ? 'dark-mode' : ''}`}>
      <h2>{t('Booking')}</h2>
      {user ? (
        <form className="booking-form" onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="service">{t('Service')}:</label>
            <select id="service" value={service} onChange={(e) => setService(e.target.value)} required disabled>
              <option value="">{t('SelectService')}</option>
              <option value="StandardRoom">{t('StandardRoom')}</option>
              <option value="LuxRoom">{t('LuxRoom')}</option>
              <option value="Sauna">{t('Sauna')}</option>
              <option value="Banya">{t('Banya')}</option>
              <option value="Kitchen">{t('Kitchen')}</option>
              <option value="Banquet">{t('Banquet')}</option>
            </select>
          </div>

          <div className="form-group">
            <label htmlFor="date">{t('Date')}:</label>
            <input id="date" type="datetime-local" value={formatDateForInput(date)} readOnly required />
          </div>

          {['StandardRoom', 'LuxRoom'].includes(service) && (
            <div className="form-group">
              <label htmlFor="endDate">{t('SelectEndDate')}:</label>
              <input id="endDate" type="datetime-local" value={formatDateForInput(endDate)} readOnly required />
            </div>
          )}

          {['Sauna', 'Banya', 'Banquet', 'Kitchen'].includes(service) && (
            <div className="form-group">
              <label htmlFor="duration">{t('SelectDurationHours')}:</label>
              <input id="duration" type="number" min="1" value={duration} readOnly required />
            </div>
          )}

          {service === 'Banquet' && (
            <>
              <div className="form-group">
                <label htmlFor="guestCount">{t('GuestCount')}:</label>
                <input id="guestCount" type="number" min="1" value={guestCount} onChange={(e) => setGuestCount(e.target.value)} />
              </div>
              <div className="form-group">
                <label>{t('BanquetExtras')}:</label>
                {banquetExtraOptions.map((extra) => (
                  <div key={extra.name}>
                    <input
                      type="checkbox"
                      id={extra.name}
                      checked={banquetExtras.includes(extra.name)}
                      onChange={() => handleBanquetExtras(extra.name)}
                    />
                    <label htmlFor={extra.name}>{t(extra.name)} - {extra.cost}</label>
                  </div>
                ))}
              </div>
            </>
          )}

          {service === 'Kitchen' && (
            <div className="form-group">
              <label>{t('KitchenMenu')}:</label>
              {kitchenMenuOptions.map((item) => (
                <div key={item.name}>
                  <input
                    type="checkbox"
                    id={item.name}
                    checked={kitchenMenu.includes(item.name)}
                    onChange={() => handleMenuSelection(item)}
                  />
                  <label htmlFor={item.name}>{t(item.name)} - {item.cost}</label>
                </div>
              ))}
            </div>
          )}

          <div className="form-group">
            <label htmlFor="cost">{t('Cost')}:</label>
            <input id="cost" type="text" value={cost} readOnly required />
          </div>

          <button type="submit" className="submit-button">
            {t('ConfirmBooking')}
          </button>
        </form>
      ) : (
        <p>{t('PleaseLogin')}</p>
      )}
      {message && (
        <p className={`message ${message.startsWith(t('BookingError')) ? 'error' : ''}`}>
          {message}
        </p>
      )}
    </div>
  );
};

export default Booking;